<template>
    <div class="manger-view">
        <h3>SWE TimeTable</h3>
<!-- <export-excel
    class   = "btn btn-default"
    :data   = "json_data"
    :fields = "json_fields"
    worksheet = "My Worksheet"
    name    = "filename.xls">
 
    Download Excel 
 
</export-excel> -->
        <div id="timesheet">
            <div class="timetable">
                <table id="swe-table">
                    <thead>
                        <tr>
                            <td>Name</td>
                            <td>Time In</td>
                            <td>Time Out</td>
                            <td>Total Hours Worked</td>
                            <td>NightDiff</td>
                            <td>OT</td>
                            <!-- <td>WFH?</td> -->
                            <td>Remarks</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="timesheet in allData" :key="timesheet.id">
                            <td>  {{ timesheet.Name }} </td>
                            <td>  {{ timesheet.Timein }} </td>
                            <td>  {{ timesheet.Timeout }} </td>
                            <td>  {{ timesheet.TotalHours }} </td>
                            <td>  {{ timesheet.NightDiff }} </td>
                            <td>  {{ timesheet.Overtime }} </td>       
                            <!-- <td>  {{ timesheet.WFH }} </td> -->
                            <td>  {{ timesheet.Remarks }} </td>
                        </tr>
                    </tbody>
                </table>
                <!-- <div v-for="timesheet in allData" :key="timesheet.id" class="timesheet">
                    {{ timesheet.Name }}
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import {mapGetters, mapActions} from 'vuex';
import excel from 'vue-excel-export'

export default {
    name: 'manager-view',
    methods: {
        ...mapActions(['retrieveData','logTime'])
    },
    computed: mapGetters(['allData']),

    created(){
        this.retrieveData()
        this.logTime()
      console.log(this.$store.Name)
    },

    data(){
        return {
            json_fields:{
                'Name': 'Name',
                'Time in': 'Timein',
                'Time out': 'Timeout',
                'Total Hours': 'TotalHours',
                'Night Diff': 'NightDiff',
                'Over Time': 'Overtime',
                'Remarks': 'Remarks'
            },
            json_data:[
                {
                'Name': this.Name,
                'Timein': this.Timein,
                'Timeout': this.Timeout,
                'TotalHours' : this.TotalHours,
                'NightDiff': this.NightDiff,
                'OverTime': this.Overtime,
                'Remarks' : this.Remarks
                }
            ],
            json_meta:[
                [
                    {
                        'key' : 'charset',
                        'value' : 'utf-8'
                    }
                ]
            ]
        }
    }
}
</script>

<style scoped>
    

</style>



